import React from 'react';
import { Icon } from 'antd';
import styled from 'styled-components';
const SpanCus = styled.span``;
const IconText = ({ text, isAns }) => (
  <SpanCus>
    {text}
    {isAns && (
      <Icon type="star-o" style={{ marginLeft: 8, color: '#eb2f96' }} />
    )}
  </SpanCus>
);
function answerList({ A, B, C, D, Ans }) {
  return [
    <IconText text={`${A}`} isAns={Ans === 'A'} key="list-vertical-star-o" />,
    <IconText text={`${B}`} isAns={Ans === 'B'} key="list-vertical-like-o" />,
    <IconText text={`${C}`} isAns={Ans === 'C'} key="list-vertical-message" />,
    <IconText text={`${D}`} isAns={Ans === 'D'} key="list-vertical-message" />,
  ];
}

export default answerList;
